@import url(https://fonts.googleapis.com/css?family=Comfortaa|Lobster|Nunito|Raleway);
h1 {
  font-size: 2.25rem;
  font-family: "Lobster", cursive; }

html {
  height: 100%; }

body {
  min-height: 100%;
  padding: 0;
  margin: 0;
  position: relative; }

body::after {
  content: '';
  display: block;
  height: 75px; }

.navigation {
  background-color: #F5F5F5 !important;
  font-weight: bold;
  padding: 15px;
  text-align: center;
  color: #000000 !important;
  font-family: "Nunito", sans-serif; }
  .navigation:hover {
    color: #fd5e53 !important; }
  .navigation__navbrand {
    font-family: "Comfortaa", cursive;
    color: #fd5e53 !important; }
  .navigation__hamburger {
    background: none !important;
    border: none !important; }
    .navigation__hamburger--white {
      background-color: #ffffff !important; }
  .navigation__branding {
    font: 48px bold;
    padding: 0; }
    @media (max-width: 767px) {
      .navigation__branding {
        padding: 12px;
        font: 20px bold; } }
  .navigation__active {
    color: #fd5e53 !important;
    border-bottom: 1px solid #fd5e53; }

.panel-footer {
  height: 75px;
  background-color: #F5F5F5;
  text-align: center;
  color: #000000;
  margin-top: 30px;
  position: absolute;
  bottom: 0;
  width: 100%; }
  .panel-footer__copyright {
    font-family: "Nunito", sans-serif;
    font-weight: bold;
    padding-top: 25px;
    display: block; }

.packages-container {
  margin-top: 30px; }
  .packages-container__header {
    font-family: "Lobster", cursive;
    font-size: 50px;
    text-align: center;
    color: #fd5e53;
    margin-bottom: 30px; }

.packages-column {
  border: 2px solid #fd5e53;
  border-radius: 5px;
  padding: 15px;
  font-family: "Raleway", sans-serif; }
  .packages-column__header {
    font-family: "Lobster", cursive; }
  .packages-column__price {
    font-weight: bolder;
    text-align: center;
    font-size: 20px; }
  .packages-column__btn {
    height: 50px;
    width: 100%;
    color: #ffffff;
    background-color: #fd5e53;
    border: 2px solid #fd5e53;
    font-size: 20px; }
    .packages-column__btn:hover {
      background-color: transparent;
      border: inherit;
      color: #000000; }

.homepageContainer {
  margin-top: 30px; }
  .homepageContainer__row {
    margin-bottom: -15px; }

.col-home {
  border: 2px solid #fd5e53;
  border-radius: 5px;
  padding: 15px;
  height: 100%;
  margin-top: 30px;
  font-family: "Raleway", sans-serif; }
  .col-home--grey {
    color: grey; }
  .col-home__header {
    font-family: "Lobster", cursive;
    color: #fd5e53;
    text-align: center;
    margin-bottom: 15px;
    font-size: 2.5rem; }
  .col-home__header-two {
    font-family: "Comfortaa", cursive;
    font-size: 1.75rem; }
  .col-home__link:hover .col-home__overlay {
    transition: 0.3s;
    -webkit-filter: brightness(50%); }
  .col-home__overlay:hover {
    transition: 0.3s;
    -webkit-filter: brightness(50%); }
  .col-home__img-text {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #ffffff;
    font-family: "Lobster", cursive;
    font-size: 3.125rem; }

.contact-container {
  margin-top: 30px; }
  .contact-container__header {
    font-size: 3.125rem;
    text-align: center;
    color: #fd5e53; }

.contact-success-container {
  margin-top: 30px;
  text-align: center;
  font-family: "Raleway", sans-serif;
  font-size: 1.5625rem; }
  .contact-success-container__header {
    color: #fd5e53;
    font-size: 3.125rem; }

.contact-form {
  padding: 30px;
  margin: auto;
  color: #000000;
  width: 60%;
  border: 2px solid #fd5e53;
  border-radius: 5px;
  margin: 30px auto 30px auto; }
  @media screen and (max-width: 767px) {
    .contact-form {
      width: 100%; } }
  .contact-form__input {
    height: 65px;
    font-size: 18px; }
    .contact-form__input--error {
      color: red; }
    .contact-form__input--required {
      color: red; }
    .contact-form__input--textarea {
      height: 150px !important; }
  .contact-form__info {
    color: grey; }
  .contact-form__btn {
    border: 2px solid #fd5e53;
    background-color: #fd5e53 !important;
    color: #ffffff;
    width: 250px;
    height: 65px;
    font-size: 20px; }
    .contact-form__btn:hover {
      border: 2px solid #fd5e53 !important;
      background-color: transparent !important;
      color: #000000; }
    @media screen and (max-width: 767px) {
      .contact-form__btn {
        width: 100%; } }

